:root {
  --header-color: #00acdc;
}

html {
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: flex;
}

img {
  display: block;
}

nav {
  display: flex;
}

.nav-list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav-link {
  color: #000;
  white-space: nowrap;
  align-items: center;
  margin-left: 20px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-link:hover {
  color: var(--header-color);
}

.nav-list li:first-child .nav-link {
  margin-left: 0;
}

.is-active {
  color: var(--header-color);
}

.nav-link.is-active:after {
  content: "";
  background-color: var(--header-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
